html {
  background-color: #1e1e1e;
  color: #ffffff;
  font-size: 62.5%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.inspecionar {
  border: 5px solid red;
}
footer,
section {
  padding: 5rem;
  max-width: 96rem;
  margin: 0 auto;
}

h2 {
  font-size: 4rem;
  font-weight: 700;
}

footer {
  flex-direction: column;
}

.about {
  background-color: #162340;
}
.itens-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav {
  padding: 2rem 6rem;
}
.nav-links {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  font-size: 1.8rem;
}

.nav-links a {
  color: #ffffff;
  text-decoration-line: none;
}
.main {
  margin-bottom: 5rem;
}
.avatar {
  border-radius: 100%;
  width: 24rem;
  height: 24rem;
  margin-right: 10rem;
}

.main-title {
  font-size: 3rem;
}

.main-description {
  margin: 4rem 0;
}
.button {
  border-radius: 0.2rem;
  background-color: #0039cc;
  padding: 1rem 2rem;
  color: #ffffff;
  text-decoration-line: none;
  font-size: 1.6rem;
}
.project {
  margin: 4rem;
}

.project-image {
  width: 24rem;
  height: 18rem;
  margin-right: 2.5rem;
  border-radius: 0.6rem;
}

.project-title {
  font-size: 3rem;
}
.footer-social {
  display: flex;
  gap: 2rem;
}
@media (max-width: 1200px) {
  .itens-center {
    flex-direction: column;
  }
  footer,
  section {
    padding: 5rem;
  }

  .main {
    margin-bottom: 2rem;
  }
  .project-image {
    width: 38rem;
    height: auto;
  }
  .nav-links {
    justify-content: center;
  }
  .avatar {
    width: 24rem;
    height: 24rem;
  }
}
